@charset "UTF-8";
@keyframes menu-open {
  0% {
    transform: scaleY(0);
    visibility: visible;
  }
  100% {
    transform: scaleY(1);
    visibility: visible;
  }
}
@keyframes menu-close {
  0% {
    transform: scaleY(1);
    visibility: visible;
  }
  99% {
    transform: scaleY(0);
    visibility: visible;
  }
  100% {
    transform: scaleY(0);
    visibility: hidden;
  }
}
@font-face {
