  margin: 0;
  width: 100%;
  cursor: text;
  outline: none;
  box-shadow: none;
  transition: 140ms all ease-in-out;
  padding: 15px 11px;
  min-height: 52px;
}
.input::placeholder {
  color: rgba(142, 152, 146, 0.7);
}
.input:hover, .input:active, .input:focus {
  border-color: #7bdb0a;
}
.input:focus:invalid, .input:active:invalid, .input--invalid, .input--invalid:hover, .input--invalid:active {
  border-color: #ff8282;
}
.input:disabled, .input:read-only {
  border-color: rgba(142, 152, 146, 0.7);
  cursor: default;
}
.input--num::-webkit-inner-spin-button, .input--num::-webkit-outer-spin-button {
  appearance: none;
}
.input--textarea {
  resize: none;
}
.input::-webkit-contacts-auto-fill-button {
  visibility: hidden;
  position: absolute;
  pointer-events: none;
  right: 0;
}

.control {
  position: relative;
  display: flex;
  gap: 12px;
  align-items: center;
}

.control__label {
  cursor: pointer;
}
@media (max-width: 767px) {
  .control__label {
    font-size: 16px;
    line-height: 19px;
  }
}
@media (min-width: 768px) {
  .control__label {
    font-size: 18px;
    line-height: 22px;
  }
}

.control__mark {
  cursor: pointer;
  position: relative;
  flex: 0 0 auto;
  aspect-ratio: 1/1;
  width: 24px;
  border: 2px solid #0f805d;
  border-radius: 5px;
}
.control__mark--checkbox::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 14px;
  height: 11px;
  background-color: transparent;
  background-image: url("data:image/svg+xml;charset=UTF-8,<svg viewBox='0 0 14 11' fill='%230F805D' xmlns='http://www.w3.org/2000/svg'><path d='M5 11 0 5.804 1.59 4.19 5 7.7 12.41 0 14 1.655 5 11Z'/></svg>");
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
  display: none;
}
.control__mark--radio {
  border-radius: 50%;
  width: 21px;
  margin: 0 1.5px;
}
.control__mark--radio::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  aspect-ratio: 1/1;
  width: 9px;
  background-color: #0f805d;
  border-radius: 50%;
  content: "";
  display: none;
}

.control__input:checked + .control__mark::before {
  display: block;
}
.control__input:hover + .control__mark, .control__input:focus + .control__mark {
  border-color: #7bdb0a;
}
.control__input:checked:hover + .control__mark--checkbox::before, .control__input:checked:focus + .control__mark--checkbox::before {
  background-image: url("data:image/svg+xml;charset=UTF-8,<svg viewBox='0 0 14 11' fill='%237BDB0A' xmlns='http://www.w3.org/2000/svg'><path d='M5 11 0 5.804 1.59 4.19 5 7.7 12.41 0 14 1.655 5 11Z'/></svg>");
}
.control__input:checked:hover + .control__mark--radio::before, .control__input:checked:focus + .control__mark--radio::before {
  background-color: #7bdb0a;
}
.control__input:active + .control__mark {
  opacity: 0.6;
}
.control__input:disabled {
  pointer-events: none;
  user-select: none;
}
.control__input:disabled ~ span {
  cursor: default;
}
.control__input:disabled + .control__mark {
  border-color: rgba(142, 152, 146, 0.7);
  opacity: 1;
}
.control__input:disabled:checked + .control__mark--checkbox::before {
  background-image: url("data:image/svg+xml;charset=UTF-8,<svg viewBox='0 0 14 11' fill='%238E9892' xmlns='http://www.w3.org/2000/svg'><path d='M5 11 0 5.804 1.59 4.19 5 7.7 12.41 0 14 1.655 5 11Z'/></svg>");
}

