.main-nav__toggle {
  cursor: pointer;
  font: inherit;
  display: block;
  background-color: transparent;
  border: none;
  margin: 0;
  right: 0;
  top: 0;
  padding: 0;
  aspect-ratio: 1/1;
  height: 100%;
  position: absolute;
  z-index: 2;
  transition: 140ms all ease-in-out;
  color: #000000;
  margin-right: 10px;
}
.main-nav__toggle:hover::before, .main-nav__toggle:hover::after {
  background-color: #7bdb0a;
}
.main-nav__toggle:active::before, .main-nav__toggle:active::after {
  background-color: rgba(15, 128, 93, 0.5);
}
.main-nav__toggle--closed::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 3px;
  width: 45px;
  height: 5px;
  background-color: #0f805d;
  box-shadow: 0 12px 0 0 #0f805d, 0 -12px 0 0 #0f805d;
}
.main-nav__toggle--closed:hover::before {
  box-shadow: 0 12px 0 0 #7bdb0a, 0 -12px 0 0 #7bdb0a;
}
.main-nav__toggle--closed:active::before {
  box-shadow: 0 12px 0 0 rgba(15, 128, 93, 0.5), 0 -12px 0 0 rgba(15, 128, 93, 0.5);
}
.main-nav__toggle--opened::before, .main-nav__toggle--opened::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  content: "";
  position: absolute;
  width: 35px;
  height: 3px;
  border-radius: 2px;
  background-color: #0f805d;
  box-shadow: none;
}
.main-nav__toggle--opened::after {
  transform: rotate(45deg);
}
.main-nav__toggle--opened::before {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav__wrapper {
    width: 100%;
    position: absolute;
    top: 100%;
    right: 0;
    animation-duration: 0.2s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    transform-origin: 0 0;
    visibility: hidden;
    transform: scaleY(0);
    background-color: #f1f6fc;
    padding: 16px 0;
    min-height: 360px;
  }
  .main-nav__wrapper--opened {
    animation-name: menu-open;
  }
  .main-nav__wrapper--closed {
    animation-name: menu-close;
  }
}
@media (min-width: 768px) {
